.topbarContainer {
    height: 50px;
    width: 100%;
    background-color: rgb(255, 136, 0);
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
}
.logo {
    font-size: 24px;
    margin-left: 20px;
    font-weight: bold;
    color: white;
    cursor: pointer;
}
.topbarLeft {
    flex:3;
}
.topbarCenter {
    flex:5;
}

.topbarRight {
    flex:4;
}
.topbarImg {
    width: 32px;
    height: 32px;
}